  -webkit-box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.125);
          box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.125);
  -webkit-transition: .25s ease;
  -o-transition: .25s ease;
